.card {
    /*background-color: rgb(65, 99, 253);*/
    background-color: rgb(9, 9, 12);
    margin: auto;
    width: 30%;
    margin-top: 40px;
    overflow: auto;
    padding: 20px 30px;
    border-radius: 10px;
}

.card-title {
    float: left;
}

.card-logo {
    float: right;
    width: 70px;
}

.card-content {
    display: block;
    clear: both;
    text-align: left;
}

.card-button {
    display: block;
    clear: both;
}

@media only screen and (max-width: 880px) {
    .card {
        width: 300px;
    }
    .card-logo {
        margin: auto;
        width: 50px;
    }
}