body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-color: #0e1015;
    color: aliceblue;
}

.container {
    width: 100%;
    text-align: center;
    position: absolute;
    top: 20%;
}

#avatar {
    width: 140px;
    border-radius: 100%;
    line-height: 0px;
}

.head-content {
    font-family: sans-serif;
}

.head-content h1 {
    line-height: 0;
}

.head-content h3 {
    line-height: 0px;
    position: absolute;
    width: 100%;
    font-size: 26px;
}

.typing {
    color: rgb(73, 95, 218);
}

.project-content {
    position: relative;
    top: 170px;
    font-family: sans-serif;
    line-height: 26px;
}

.about-content {
    position: relative;
    font-family: sans-serif;
    top: 290px;
}

.footer {
    bottom: 0;
    position: relative;
    width: 100%;
    top: 340px;
    display: block;
    margin-top: 10px;
    font-family: sans-serif;
    font-size: 12px;
}

a {
    color: aliceblue;
    text-decoration: aliceblue;
}

.navbar li {
    list-style: none;
    font-family: sans-serif;
    display: inline-block;
    margin: 10px;
}

.navbar li:hover {
    color: rgb(70, 70, 202);
}

.navbar li a:hover {
    color: rgb(70, 70, 202);
}

@media only screen and (max-width: 880px) {
    .navbar {
        text-align: center;
    }
}